@charset "UTF-8";
/* 版心样式 */
.con {
  width: 1239px;
  margin: 0 auto; }

header {
  height: 110px;
  border-bottom: 1px solid #fee6bc;
  padding-top: 40px; }
  header .headerCon {
    height: 110px; }
    header .headerCon h1 {
      width: 355px;
      height: 69px;
      float: left; }
    header .headerCon p {
      float: right;
      margin: 78px 4px 0 0; }
      header .headerCon p a {
        display: block;
        width: 160px;
        height: 20px;
        color: #888888;
        padding-left: 31px;
        background: url(images/login_head.png) no-repeat left center; }

.tips {
  height: 37px;
  background-color: #fff4e3; }
  .tips .tipsCon {
    height: 37px;
    background-color: #fff4e3; }
    .tips .tipsCon p {
