.banner {
  height: 600px;
  background-color: #fff1e8; }
  .banner .bannerCon {
    height: 570px;
    background-color: #fff1e8;
    padding-top: 30px;
    background: url(images/login_banner_m.png) no-repeat 130px 95px; }
    .banner .bannerCon .login {
      width: 438px;
      min-height: 450px;
      background-color: white;
      float: right; }
      .banner .bannerCon .login .loginTop {
        height: 67px;
        border-bottom: 1px solid #f4f4f4;
        background: url(data:image/png;base64,) no-repeat center center; }
        .banner .bannerCon .login .loginTop span {
          display: block;
          float: left;
          width: 50%;
          height: 67px;
          font-size: 22px;
          color: #5e5e5e;
          text-align: center;
          line-height: 67px;
          cursor: pointer; }
        .banner .bannerCon .login .loginTop span.select {
          color: #e3101e; }
      .banner .bannerCon .login .loginPwd {
        display: none;
        height: 347px; }
        .banner .bannerCon .login .loginPwd form {
          width: 100%;
          height: 100%; }
          .banner .bannerCon .login .loginPwd form i {
            display: block;
            margin-top: 37px;
            padding-left: 40px;
            height: 16px;
            line-height: 16px;
            color: red;
            margin-bottom: 4px; }
          .banner .bannerCon .login .loginPwd form p:nth-of-type(1) {
            text-align: center; }
            .banner .bannerCon .login .loginPwd form p:nth-of-type(1) input {
              display: inline-block;
              width: 360px;
              height: 50px;
              border: 1px solid #a5a5a5;
              margin: 0 0 13px 0;
              font-size: 18px;
              text-indent: 14px; }
          .banner .bannerCon .login .loginPwd form p:nth-of-type(2) {
            text-align: center; }
            .banner .bannerCon .login .loginPwd form p:nth-of-type(2) input {
              display: inline-block;
              width: 360px;
              height: 50px;
              border: 1px solid #a5a5a5;
              font-size: 18px;
              text-indent: 14px; }
          .banner .bannerCon .login .loginPwd form p:nth-of-type(3) {
            margin: 27px 0 0 37px; }
          .banner .bannerCon .login .loginPwd form p:nth-of-type(4) {
            margin: 28px 27px 0 0; }
            .banner .bannerCon .login .loginPwd form p:nth-of-type(4) a:first-child {
              color: #5e5e5e;
              padding-right: 14px;
              border-right: 1px solid #e3d6cc; }
              .banner .bannerCon .login .loginPwd form p:nth-of-type(4) a:first-child label {
                color: #5e5e5e;
                padding-left: 7px; }
            .banner .bannerCon .login .loginPwd form p:nth-of-type(4) a:last-child {
              color: #2875cc;
              padding-left: 14px; }
          .banner .bannerCon .login .loginPwd form p:nth-of-type(5) {
            text-align: center; }
            .banner .bannerCon .login .loginPwd form p:nth-of-type(5) input {
              display: inline-block;
              width: 360px;
              height: 50px;
              font-size: 18px;
              text-align: center;
              line-height: 50px;
              color: #fffcff;
              background-color: #ff5757;
              border-radius: 3px;
              cursor: pointer;
              margin-top: 17px; }
          .banner .bannerCon .login .loginPwd form p:nth-of-type(6) {
            margin: 40px 0 0 0;
            padding-left: 38px; }
            .banner .bannerCon .login .loginPwd form p:nth-of-type(6) a {
              color: #2875cc; }
      .banner .bannerCon .login .loginMark {
        position: relative;
        min-height: 330px; }
        .banner .bannerCon .login .loginMark div {
          height: 260px; }
          .banner .bannerCon .login .loginMark div:hover img:first-of-type {
            left: 25px; }
          .banner .bannerCon .login .loginMark div:hover img:last-of-type {
            opacity: 1; }
        .banner .bannerCon .login .loginMark img:first-of-type {
          padding: 11px;
          width: 186px;
          height: 186px;
          border: 1px solid #e6e6e6;
          position: absolute;
          top: 30px;
          left: 118px;
          background-color: white;
          z-index: 1;
          -webkit-transition: left 0.4s ease;
          transition: left 0.4s ease; }
        .banner .bannerCon .login .loginMark img:nth-of-type(2) {
          width: 168px;
          height: 223px;
          position: absolute;
          top: 21px;
          right: 12px;
          opacity: 0;
          -webkit-transition: opacity 0.1s ease;
          transition: opacity 0.1s ease; }
        .banner .bannerCon .login .loginMark p {
          text-align: center; }
          .banner .bannerCon .login .loginMark p span {
            color: #ff5757;
            padding-left: 6px; }
        .banner .bannerCon .login .loginMark p:last-of-type {
          text-align: right;
          margin-bottom: 10px; }
          .banner .bannerCon .login .loginMark p:last-of-type a {
            color: #006699;
            padding-right: 35px; }
      .banner .bannerCon .login .moreLog {
        position: relative;
        height: 60px;
        border: 1px solid #f0f0f0;
        background-color: #fcfcfc;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        padding: 0 0 0 38px; }
        .banner .bannerCon .login .moreLog ul {
          width: 398px;
          height: 60px;
          line-height: 58px; }
        .banner .bannerCon .login .moreLog ul li i {
          padding-right: 12px;
          border-right: 1px solid #e6e6e6;
          color: #5e5e5e; }
        .banner .bannerCon .login .moreLog ul li {
          float: left;
          height: 60px; }
          .banner .bannerCon .login .moreLog ul li a {
            line-height: 58px;
            padding: 0 12px;
            border-right: 1px solid #e6e6e6;
            color: #5e5e5e; }
        .banner .bannerCon .login .moreLog ul li:nth-child(5) a {
          border-right: 0; }
        .banner .bannerCon .login .moreLog ul li:nth-child(6) a {
          padding-left: 0; }
        .banner .bannerCon .login .moreLog em {
          display: block;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          border: 1px solid #e6e6e6;
          border-radius: 10px;
          color: #bbbbbb;
          background-color: #eeeeee;
          width: 24px;
          height: 23px;
          position: absolute;
          right: 15px;
          top: 17px;
          text-align: center;
          line-height: 23px;
          cursor: pointer; }

